import axios from 'axios';
import 'abortcontroller-polyfill/dist/abortcontroller-polyfill-only'

export const indexServices = {
    getUsers
  };

function getUsers (data){
    console.log(data)
    return axios.get(`https://randomuser.me/api/`, data)
    .then (data => {
        return data;
    })
    
}


  